export interface SkillDetail {
  abbr: string;
  name: string;
  icon: string;
  type: string;
  speed: string;
  description: string;
}

export const getSkillByAbbr = (abbr: string): SkillDetail | undefined => {
  return ALL_SKILLS.find(skill => skill.abbr === abbr);
};

export interface StatusDetail {
    name: string;
    description: string;
}

