import { Player } from './Player';

export class Battle {
    static SHORT: number = 0; // 0= 1=

    dataPc1: string;
    dataPc2: string;
    text: string = "";

    private turn: number;
    public pc1: Player | null = null;
    public pc2: Player | null = null;

    constructor(pc1Data: string, pc2Data: string) {
        this.dataPc1 = pc1Data;
        this.dataPc2 = pc2Data;
        this.turn = 1; // Initialize turn here
    }

    start(): number {
        // setting.txtWeb
        // 
        Battle.SHORT = 0;

        this.pc1 = new Player(this.dataPc1);
        this.pc2 = new Player(this.dataPc2);
        const pc1 = this.pc1;
        const pc2 = this.pc2;

        this.text += "=============================================\n\n";
        this.text += pc1.playerName + "VS" + pc2.playerName + "\n\n";

        this.text += "\n\n";

        for (this.turn = 1; this.turn >= 0; this.turn++) { // 

            let sta1 = 0; let sta2 = 0;
            let statusPc1 = ""; let statusPc2 = "";

            this.text += `${this.turn}\n\n`;

            if (Battle.SHORT === 0) {
                for (let i = 0; i < pc1.getSkillsLength(); i++) this.text += `${pc1.name[i]}`;
            } else {
                for (let i = 0; i < pc1.getSkillsLength(); i++) this.text += pc1.skill[i];
            }

            this.text += `${pc1.playerName}`;

            if (pc1.stan === 1) { sta1 = 1; statusPc1 += ""; }
            if (pc1.roubai === 1) { sta1 = 1; statusPc1 += ""; }
            if (pc1.suijaku === 1) { sta1 = 1; statusPc1 += ""; }
            if (pc1.kakugo === 1) { sta1 = 1; statusPc1 += ""; }
            if (pc1.bouheki >= 1) { sta1 = 1; for (let i = 0; i < pc1.bouheki; i++) statusPc1 += ""; }
            if (pc1.gekirin >= 1) { sta1 = 1; for (let i = 0; i < pc1.gekirin; i++) statusPc1 += ""; }
            if (pc1.musou === 1) { sta1 = 1; statusPc1 += ""; }
            if (pc1.sensei === 1) { sta1 = 1; statusPc1 += ""; }

            if (sta1 === 1) this.text += `${statusPc1}`;

            this.text += "\n";

            if (Battle.SHORT === 0) {
                for (let i = 0; i < pc2.getSkillsLength(); i++) this.text += `${pc2.name[i]}`;
            } else {
                for (let i = 0; i < pc2.getSkillsLength(); i++) this.text += pc2.skill[i];
            }


            this.text += `${pc2.playerName}`;

            if (pc2.stan === 1) { sta2 = 1; statusPc2 += ""; }
            if (pc2.roubai === 1) { sta2 = 1; statusPc2 += ""; }
            if (pc2.suijaku === 1) { sta2 = 1; statusPc2 += ""; }
            if (pc2.kakugo === 1) { sta2 = 1; statusPc2 += ""; }
            if (pc2.bouheki >= 1) { sta2 = 1; for (let i = 0; i < pc2.bouheki; i++) statusPc2 += ""; }
            if (pc2.gekirin >= 1) { sta2 = 1; for (let i = 0; i < pc2.gekirin; i++) statusPc2 += ""; }
            if (pc2.musou === 1) { sta2 = 1; statusPc2 += ""; }
            if (pc2.sensei === 1) { sta2 = 1; statusPc2 += ""; }

            if (sta2 === 1) this.text += `${statusPc2}`;

            this.text += "\n";

            // 

            let startFlag = 0; // 

            this.text += "\n";
            this.text += "\n";
            this.text += "\n";

            if (this.turn <= pc1.getSkillsLength()) {

                if (pc1.skill[this.turn - 1] === "") {
                    this.text += `${pc1.playerName}${pc1.name[this.turn - 1]}${this.turn}\n`;
                    this.text += `${pc1.playerName}\n`;
                    pc1.musou = 1;
                    pc1.limited[this.turn - 1] = 2;
                    startFlag = 1;
                }

                if (pc1.skill[this.turn - 1] === "") {
                    this.text += `${pc1.playerName}${pc1.name[this.turn - 1]}${this.turn}\n`;
                    this.text += `${pc1.playerName}\n`;
                    pc1.sensei = 1;
                    startFlag = 1;
                }

            }

            if (this.turn <= pc2.getSkillsLength()) {
                if (pc2.skill[this.turn - 1] === "") {
                    this.text += `${pc2.playerName}${pc2.name[this.turn - 1]}${this.turn}\n`;
                    this.text += `${pc2.playerName}\n`;
                    pc2.musou = 1;
                    pc2.limited[this.turn - 1] = 2;
                    startFlag = 1;
                }

                if (pc2.skill[this.turn - 1] === "") {
                    this.text += `${pc2.playerName}${pc2.name[this.turn - 1]}${this.turn}\n`;
                    this.text += `${pc2.playerName}\n`;
                    pc2.sensei = 1;
                    startFlag = 1;
                }

            }

            if (startFlag === 1) this.text += "\n";

            // 

            this.text += "\n";
            this.text += "\n";

            let speedPc1 = 0; let speedPc2 = 0;
            let speedBufPc1 = 0; let speedBufPc2 = 0;

            for (let i = 0; i < pc1.getSkillsLength(); i++) {

                speedBufPc1 = 0; speedBufPc2 = 0;

                if (pc1.stan === 1 && pc2.stan === 0) {
                    this.text += "  ";
                    speedPc1 = -999;
                    break;
                }

                if (pc1.sensei === 1 && pc2.sensei === 0) {
                    this.text += "  ";
                    speedPc1 = 999;
                    break;
                }

                if (pc1.type[i] === Player.ATTACK && !(i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") && !(pc1.skill[i] === "" && Math.floor(this.turn / 2) * 2 !== this.turn)) {

                    if (pc1.skill[i] === "") {
                        pc1.damage[i] = 0;
                        for (let j = 0; j < pc1.getSkillsLength(); j++) {
                            if (pc1.type[j] === Player.ATTACK && !(j < pc1.getSkillsLength() - 1 && pc1.skill[j + 1] === "")) {
                                pc1.damage[i]++;
                            }
                        }
                        if (pc1.speed[i] < 0) pc1.speed[i] = 0;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") speedBufPc1 += 1;
                    if (pc1.kakugo === 1) speedBufPc1 += 2;

                    this.text += `${pc1.name[i]}${i + 1}${pc1.speed[i] + speedBufPc1}`;
                    speedPc1 = pc1.speed[i] + speedBufPc1;
                    break;
                }

                if (pc1.type[i] === Player.BUFF) {
                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") speedBufPc1 += 1;

                    this.text += `${pc1.name[i]}${i + 1}${pc1.speed[i] + speedBufPc1}`;
                    speedPc1 = pc1.speed[i] + speedBufPc1;
                    break;
                }

                if (i === pc1.getSkillsLength() - 1) {
                    this.text += " 0";
                    speedPc1 = 0;
                    break;

                }
            }

            this.text += `${pc1.playerName}\n`;


            for (let i = 0; i < pc2.getSkillsLength(); i++) {

                if (pc2.stan === 1 && pc1.stan === 0) {
                    this.text += "  ";
                    speedPc2 = -999;
                    break;
                }


                if (pc2.sensei === 1 && pc1.sensei === 0) {
                    this.text += "  ";
                    speedPc2 = 999;
                    break;
                }


                if (pc2.type[i] === Player.ATTACK && !(i < pc2.getSkillsLength() - 1 && pc2.skill[i + 1] === "") && !(pc2.skill[i] === "" && Math.floor(this.turn / 2) * 2 !== this.turn)) {

                    if (pc2.skill[i] === "") {
                        pc2.damage[i] = 0;
                        for (let j = 0; j < pc2.getSkillsLength(); j++) {
                            if (pc2.type[j] === Player.ATTACK && !(j < pc1.getSkillsLength() - 1 && pc1.skill[j + 1] === "")) {
                                pc2.damage[i]++;
                            }
                        }
                        if (pc2.speed[i] < 0) pc2.speed[i] = 0;
                    }

                    if (i < pc2.getSkillsLength() - 1 && pc2.skill[i + 1] === "") speedBufPc2 += 1;
                    if (pc2.kakugo === 1) speedBufPc2 += 2;

                    this.text += `${pc2.name[i]}${i + 1}${pc2.speed[i] + speedBufPc2}`;
                    speedPc2 = pc2.speed[i] + speedBufPc2;
                    break;
                }

                if (pc2.type[i] === Player.BUFF) {
                    if (i < pc2.getSkillsLength() - 1 && pc2.skill[i + 1] === "") speedBufPc2 += 1;

                    this.text += `${pc2.name[i]}${i + 1}${pc2.speed[i] + speedBufPc2}`;
                    speedPc2 = pc2.speed[i] + speedBufPc2;
                    break;
                }

                if (i === pc2.getSkillsLength() - 1) {
                    this.text += " 0";
                    speedPc2 = 0;
                    break;

                }
            }

            this.text += `${pc2.playerName}\n`;

            let judgeResult: number;
            this.text += "\n";
            if (speedPc1 > speedPc2) {
                this.text += `${pc1.playerName}\n`;
                this.text += "\n";

                this.text += `${pc1.playerName}\n`;
                this.text += "\n";
                this.rinko(pc1, pc2);
                this.attack(pc1, pc2);
                this.text += "\n";
                if (this.effect(pc1, pc2) + this.effect(pc2, pc1) >= 1) this.text += "\n";
                if (this.breakup(pc1) + this.breakup(pc2) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 2);
                if (judgeResult > 0) return judgeResult;

                this.text += `${pc2.playerName}\n`;
                this.text += "\n";
                this.rinko(pc2, pc1);
                this.attack(pc2, pc1);
                this.text += "\n";
                if (this.effect(pc2, pc1) + this.effect(pc1, pc2) >= 1) this.text += "\n";
                if (this.breakup(pc2) + this.breakup(pc1) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 1);
                if (judgeResult > 0) return judgeResult;
            }

            if (speedPc1 < speedPc2) {
                this.text += `${pc2.playerName}\n`;
                this.text += "\n";

                this.text += `${pc2.playerName}\n`;
                this.text += "\n";
                this.rinko(pc2, pc1);
                this.attack(pc2, pc1);
                this.text += "\n";
                if (this.effect(pc2, pc1) + this.effect(pc1, pc2) >= 1) this.text += "\n";
                if (this.breakup(pc2) + this.breakup(pc1) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 1);
                if (judgeResult > 0) return judgeResult;

                this.text += `${pc1.playerName}\n`;
                this.text += "\n";
                this.rinko(pc1, pc2);
                this.attack(pc1, pc2);
                this.text += "\n";
                if (this.effect(pc1, pc2) + this.effect(pc2, pc1) >= 1) this.text += "\n";
                if (this.breakup(pc1) + this.breakup(pc2) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 2);
                if (judgeResult > 0) return judgeResult;
            }

            // : 
            if (speedPc1 === speedPc2) {
                this.text += `${pc1.playerName}\n`;
                this.text += "\n";

                this.text += `${pc1.playerName}\n`;
                this.text += "\n";
                this.rinko(pc1, pc2);
                this.attack(pc1, pc2);
                this.text += "\n";
                if (this.effect(pc1, pc2) + this.effect(pc2, pc1) >= 1) this.text += "\n";
                if (this.breakup(pc1) + this.breakup(pc2) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 2);
                if (judgeResult > 0) return judgeResult;

                this.text += `${pc2.playerName}\n`;
                this.text += "\n";
                this.rinko(pc2, pc1);
                this.attack(pc2, pc1);
                this.text += "\n";
                if (this.effect(pc2, pc1) + this.effect(pc1, pc2) >= 1) this.text += "\n";
                if (this.breakup(pc2) + this.breakup(pc1) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 1);
                if (judgeResult > 0) return judgeResult;
            }


            //  (: )

            let rengekiPc1 = 0; let rengekiPc2 = 0;

            for (let i = 0; i < pc1.getSkillsLength(); i++) {
                if (pc1.skill[i] === "") {
                    this.text += "\n";
                    this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;
                    rengekiPc1 = 1;
                    break;
                }
            }

            for (let i = 0; i < pc2.getSkillsLength(); i++) {
                if (pc2.skill[i] === "") {
                    this.text += "\n";
                    this.text += `${pc2.playerName}${pc2.name[i]}${i + 1}\n`;
                    rengekiPc2 = 1;
                    break;
                }
            }

            if (rengekiPc1 === 1) {
                this.text += "\n";
                this.text += `${pc1.playerName}\n`;
                this.text += "\n";
                this.rinko(pc1, pc2);
                this.attack(pc1, pc2);
                this.text += "\n";
                if (this.effect(pc1, pc2) + this.effect(pc2, pc1) >= 1) this.text += "\n";
                if (this.breakup(pc1) + this.breakup(pc2) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 2);
                if (judgeResult > 0) return judgeResult;
            }

            if (rengekiPc2 === 1) {
                this.text += "\n";
                this.text += `${pc2.playerName}\n`;
                this.text += "\n";
                this.rinko(pc2, pc1);
                this.attack(pc2, pc1);
                this.text += "\n";
                if (this.effect(pc2, pc1) + this.effect(pc1, pc2) >= 1) this.text += "\n";
                if (this.breakup(pc2) + this.breakup(pc1) >= 1) this.text += "\n";
                judgeResult = this.judge(pc1, pc2, 1);
                if (judgeResult > 0) return judgeResult;
            }

            //

            let endFlag = 0; // 

            this.text += "\n";
            this.text += "\n";

            const endLogBefore = this.text;

            // 

            if (pc1.musou === 1) {
                this.text += `${pc1.playerName}\n`;
                pc1.musou = 0;
                endFlag = 1;
            }

            if (pc2.musou === 1) {
                this.text += `${pc2.playerName}\n`;
                pc2.musou = 0;
                endFlag = 1;
            }

            // 

            if (pc1.sensei === 1) {
                this.text += `${pc1.playerName}\n`;
                pc1.sensei = 0;
                endFlag = 1;
            }

            if (pc2.sensei === 1) {
                this.text += `${pc2.playerName}\n`;
                pc2.sensei = 0;
                endFlag = 1;
            }

            // 

            for (let i = 0; i < pc1.getSkillsLength(); i++) {
                if (pc1.kage[i] === 1) {
                    pc1.kage[i] = 0;
                    pc1.scar[i] = 1;
                }
            }

            for (let i = 0; i < pc2.getSkillsLength(); i++) {
                if (pc2.kage[i] === 1) {
                    pc2.kage[i] = 0;
                    pc2.scar[i] = 1;
                }
            }


            // 

            for (let i = 0; i < pc1.getSkillsLength(); i++) {
                if (pc1.limited[i] === 2) {
                    pc1.limited[i] = 1;
                    pc1.scar[i] = 1;
                }
            }

            for (let i = 0; i < pc2.getSkillsLength(); i++) {
                if (pc2.limited[i] === 2) {
                    pc2.limited[i] = 1;
                    pc2.scar[i] = 1;
                }
            }

            if (this.breakup(pc1) + this.breakup(pc2) >= 1) endFlag = 1;

            // 
            if (pc1.suijaku === 1) {
                for (let i = 0; i < pc1.getSkillsLength(); i++) {
                    if (pc1.type[i] !== Player.NONE && pc1.skill[i] !== "") {
                        this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}${i + 1}\n`;

                        pc1.skill[i] = "";
                        pc1.name[i] = "";
                        pc1.type[i] = Player.ENCHANT;
                        pc1.speed[i] = 0;
                        pc1.damage[i] = 0;
                        pc1.scar[i] = 0;
                        pc1.limited[i] = 0;

                        endFlag = 1;
                        break;
                    }
                }
            }

            if (pc2.suijaku === 1) {
                for (let i = 0; i < pc2.getSkillsLength(); i++) {
                    if (pc2.type[i] !== Player.NONE && pc2.skill[i] !== "") {
                        this.text += `${pc2.playerName}${pc2.name[i]}${i + 1}${i + 1}\n`;

                        pc2.skill[i] = "";
                        pc2.name[i] = "";
                        pc2.type[i] = Player.ENCHANT;
                        pc2.speed[i] = 0;
                        pc2.damage[i] = 0;
                        pc2.scar[i] = 0;
                        pc2.limited[i] = 0;

                        endFlag = 1;
                        break;
                    }
                }
            }

            if (this.text === endLogBefore) {
                this.text += "\n";
            }

            if (endFlag === 1) this.text += "\n";

            judgeResult = this.judge(pc1, pc2, 3);
            if (judgeResult > 0) return judgeResult;

            this.text += "\n";

        }

        return 0;
    }

    rinko(pc1: Player, pc2: Player): void {

        for (let i = 0; i < pc1.getSkillsLength(); i++) {
            if (pc1.skill[i] === "") {
                this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;

                if (pc1.stan === 1) {
                    this.text += `${pc1.playerName}\n`;
                    pc1.stan = 0;
                }
                if (pc1.suijaku === 1) {
                    this.text += `${pc1.playerName}\n`;
                    pc1.suijaku = 0;
                }
                // if (pc1.roubai === 1) {
                //     this.text += `${pc1.playerName}\n`;
                //     pc1.roubai = 0;
                // }
                // if (pc1.kakugo === 1) {
                //     this.text += `${pc1.playerName}\n`;
                //     pc1.kakugo = 0;
                // }
                // if (pc1.bouheki >= 1) {
                //     this.text += `${pc1.playerName}\n`;
                //     pc1.bouheki = 0;
                // }
                // if (pc1.gekirin >= 1) {
                //     this.text += `${pc1.playerName}\n`;
                //     pc1.gekirin = 0;
                // }
                // if (pc1.musou === 1) {
                //     this.text += `${pc1.playerName}\n`;
                //     pc1.musou = 0;
                // }
                // if (pc1.sensei === 1) {
                //     this.text += `${pc1.playerName}\n`;
                //     pc1.sensei = 0;
                // }


                // if (pc2.stan === 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.stan = 0;
                // }
                // if (pc2.suijaku === 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.suijaku = 0;
                // }
                // if (pc2.roubai === 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.roubai = 0;
                // }
                // if (pc2.kakugo === 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.kakugo = 0;
                // }
                // if (pc2.bouheki >= 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.bouheki = 0;
                // }
                // if (pc2.gekirin >= 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.gekirin = 0;
                // }
                // if (pc2.musou === 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.musou = 0;
                // }
                // if (pc2.sensei === 1) {
                //     this.text += `${pc2.playerName}\n`;
                //     pc2.sensei = 0;
                // }

                this.text += "\n";
                break;
            }
        }

    }

    attack(pc1: Player, pc2: Player): void {

        let i: number; // For initial skill determination loops
        let j: number; // For nested loops

        let use = 0;
        let damage = 0; let damageBuf = 0;
        let bonda = 0;
        let uragasumi = 0;
        let speedPc1 = 0; // let speedPc2 = 0; ()
        let speedBufPc1 = 0; // let speedBufPc2 = 0; ()

        // 

        for (i = 0; i < pc1.getSkillsLength(); i++) {
            if (pc1.skill[i] === "") {
                this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;
                this.text += "\n";
                uragasumi = 1;
                break;
            }
        }

        if (uragasumi === 0) {

            for (i = 0; i < pc1.getSkillsLength(); i++) {

                if (pc1.type[i] === Player.ATTACK && !(i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") && !(pc1.skill[i] === "" && Math.floor(this.turn / 2) * 2 !== this.turn)) {
                    this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;


                    // 
                    if (pc1.limited[i] === 1) pc1.limited[i] = 2;

                    if (pc1.skill[i] === "") {
                        pc1.damage[i] = 0;
                        for (j = 0; j < pc1.getSkillsLength(); j++) {
                            if (pc1.type[j] === Player.ATTACK && !(j < pc1.getSkillsLength() - 1 && pc1.skill[j + 1] === "")) {
                                pc1.damage[i]++;
                            }
                        }
                        if (pc1.speed[i] < 0) pc1.speed[i] = 0;
                    }

                    if (pc1.skill[i] === "") {
                        pc1.damage[i] = this.turn;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}1\n`;
                        damageBuf++;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}+1\n`;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}1\n`;
                        pc1.limited[i + 1] = 2;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}2\n`;
                        pc1.limited[i + 1] = 2;
                    }

                    if (pc1.kakugo === 1) {
                        this.text += `1+2\n`;
                        damageBuf++;
                    }

                    if (pc1.gekirin > 0) {
                        this.text += `${pc1.gekirin}\n`;
                        damageBuf += pc1.gekirin;
                        pc1.gekirin = 0;
                    }

                    // 
                    if (pc1.roubai === 1) this.text += "0\n";

                    damage = pc1.damage[i] + damageBuf;
                    break;
                }

                if (pc1.type[i] === Player.BUFF) {
                    this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}2\n`;
                        pc1.limited[i + 1] = 2;
                    }

                    // 
                    if (pc1.limited[i] === 1) pc1.limited[i] = 2;
                    break;
                }


                if (i === pc1.getSkillsLength() - 1) {
                    this.text += `${pc1.playerName}0\n`;
                    bonda = 1;
                    damage = 1;
                    break;
                }
            }

            use = i;
        }

        // 

        if (uragasumi === 1) {

            for (i = pc1.getSkillsLength() - 1; i >= 0; i--) {

                if (pc1.type[i] === Player.ATTACK && !(i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") && !(pc1.skill[i] === "" && Math.floor(this.turn / 2) * 2 !== this.turn)) {
                    this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;


                    // 
                    if (pc1.limited[i] === 1) pc1.limited[i] = 2;

                    if (pc1.skill[i] === "") {
                        pc1.damage[i] = 0;
                        for (j = 0; j < pc1.getSkillsLength(); j++) {
                            if (pc1.type[j] === Player.ATTACK && !(j < pc1.getSkillsLength() - 1 && pc1.skill[j + 1] === "")) {
                                pc1.damage[i]++;
                            }
                        }
                        if (pc1.speed[i] < 0) pc1.speed[i] = 0;
                    }

                    if (pc1.skill[i] === "") {
                        pc1.damage[i] = this.turn;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}1\n`;
                        damageBuf++;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}+1\n`;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}1\n`;
                        pc1.limited[i + 1] = 2;
                    }

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}2\n`;
                    }


                    if (pc1.kakugo === 1) {
                        this.text += `1+2\n`;
                        damageBuf++;
                    }

                    if (pc1.gekirin > 0) {
                        this.text += `${pc1.gekirin}\n`;
                        damageBuf += pc1.gekirin;
                        pc1.gekirin = 0;
                    }

                    // 
                    if (pc1.roubai === 1) this.text += "0\n";

                    damage = pc1.damage[i] + damageBuf;
                    break;
                }

                if (pc1.type[i] === Player.BUFF) {
                    this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;

                    if (i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                        this.text += `${i + 2}2\n`;
                    }

                    // 
                    if (pc1.limited[i] === 1) pc1.limited[i] = 2;
                    break;
                }

                if (i === 0) {
                    this.text += `${pc1.playerName}0\n`;
                    bonda = 1;
                    damage = 1;
                    break;
                }
            }

            use = i;
        }

        // 

        if (use < pc1.getSkillsLength() - 1 && pc1.skill[use + 1] === "") speedBufPc1 += 1;
        if (pc1.kakugo === 1) speedBufPc1 += 2;
        speedPc1 = pc1.speed[use] + speedBufPc1;
        if (pc1.roubai === 1) speedPc1 = 0;
        if (bonda === 1) speedPc1 = 0;

        if (damage > 0) this.text += `${pc2.playerName}${damage}${speedPc1}\n\n`;

        // 

        let target = 0; // 
        let suspend = 0; // 
        let counterOn = 0; // 
        let penetrate = 0; // 

        for (let i_damage_loop = 0; i_damage_loop < damage; i_damage_loop++) {

            if (pc2.musou === 1) {
                this.text += `${pc2.playerName}\n`;
                break;
            }

            if (pc2.bouheki >= 1) {
                this.text += `${pc2.playerName}\n`;
                const removeCount = Math.ceil(pc2.bouheki / 2);
                this.text += `${pc2.playerName}${removeCount}\n`;
                pc2.bouheki -= removeCount;
                break;
            }

            // 
            target = 0;
            suspend = 0;
            for (let j = 0; j < pc2.getSkillsLength(); j++) {
                if (pc2.type[j] !== Player.NONE && pc2.scar[j] !== 2) {
                    target = j;
                    break;
                }
                if (j === pc2.getSkillsLength() - 1) {
                    suspend = 1;
                    break;
                }
            }

            // 
            if (pc1.skill[use] === "") {
                let bestDiff = 999;
                let bestTarget = -1;
                let useLV = use + 1;

                // 
                for (let j = 0; j < pc2.getSkillsLength(); j++) {
                    if (pc2.type[j] !== Player.NONE && pc2.scar[j] !== 2) {
                        let targetLV = j + 1;
                        let diff = Math.abs(useLV - targetLV);

                        if (diff < bestDiff) {
                            bestDiff = diff;
                            bestTarget = j;
                        } else if (diff === bestDiff) {
                            // LV
                            if (targetLV > (bestTarget + 1)) {
                                bestTarget = j;
                            }
                        }
                    }
                }

                if (bestTarget !== -1) {
                    target = bestTarget;
                } else {
                    suspend = 1;
                }
            }

            if (suspend === 1) {
                suspend = 0;
                break;
            }

            // 

            this.text += `${pc2.playerName}${pc2.name[target]}${target + 1}\n`;
            pc2.scar[target] = 2;

            let target2 = 0; // 
            let suspend2 = 0; // 

            // 
            if (pc2.type[target] === Player.COUNTER || (pc2.type[target] === Player.ATTACK && (target < pc2.getSkillsLength() - 1 && pc2.skill[target + 1] === ""))) {

                if (use < pc1.getSkillsLength() - 1 && pc1.skill[use + 1] === "" && penetrate === 0) {
                    this.text += `${pc1.name[use + 1]}${use + 2}${pc2.playerName}${pc2.name[target]}${target + 1}\n`;
                    penetrate = 1;

                } else {

                    let speedPc2Counter = 0;
                    let speedBufPc2Counter = 0;

                    // 

                    if (pc2.skill[target] === "") {
                        pc2.damage[target] = 0;
                        for (let i2 = 0; i2 < pc2.getSkillsLength(); i2++) {
                            if (pc2.type[i2] === Player.ATTACK && !(i2 < pc2.getSkillsLength() - 1 && pc2.skill[i2 + 1] === "")) {
                                pc2.damage[target]++;
                            }
                        }
                        if (pc2.speed[target] < 0) pc2.speed[target] = 0;
                    }

                    if (pc2.skill[target] === "") {
                        pc2.damage[target] = this.turn;
                    }


                    if (target < pc2.getSkillsLength() - 1 && pc2.skill[target + 1] === "") speedBufPc2Counter += 1;
                    if (pc2.kakugo === 1) speedBufPc2Counter += 2;
                    speedPc2Counter = pc2.speed[target] + speedBufPc2Counter;


                    // 

                    if (speedPc2Counter >= speedPc1 || bonda === 1) {

                        counterOn = 1;

                        if (pc2.kakugo === 1) {
                            this.text += `1+2\n`;
                        }

                        this.text += `${pc2.playerName}${pc2.name[target]}${target + 1}${speedPc2Counter}\n`;

                        // 
                        if (pc2.limited[target] === 1) pc2.limited[target] = 2;

                        if (pc2.skill[target] === "" || pc2.skill[target] === "" || pc2.skill[target] === "" ||
                            pc2.skill[target] === "" || pc2.skill[target] === "" || pc2.skill[target] === "" || pc2.skill[target] === "" ||
                            pc2.skill[target] === "" || pc2.skill[target] === "" || pc2.skill[target] === "" ||
                            pc2.skill[target] === "" || pc2.skill[target] === "" || pc2.skill[target] === "") { // 

                            if (pc2.skill[target] === "") {
                                pc2.damage[target] = speedPc1;
                            }

                            // 
                            let counterDamage = pc2.damage[target];
                            if (pc2.kakugo === 1) counterDamage += 1;

                            for (let i2 = 0; i2 < counterDamage; i2++) {

                                if (pc1.musou === 1) {
                                    this.text += `${pc1.playerName}\n`;
                                    break;
                                }

                                if (pc1.bouheki >= 1) {
                                    this.text += `${pc1.playerName}\n`;
                                    const removeCount = Math.ceil(pc1.bouheki / 2);
                                    this.text += `${pc1.playerName}${removeCount}\n`;
                                    pc1.bouheki -= removeCount;
                                    break;
                                }

                                // 
                                target2 = 0;
                                suspend2 = 0;
                                for (let j2 = 0; j2 < pc1.getSkillsLength(); j2++) {
                                    if (pc1.type[j2] !== Player.NONE && pc1.scar[j2] !== 2) {
                                        target2 = j2;
                                        break;
                                    }
                                    if (j2 === pc1.getSkillsLength() - 1) {
                                        suspend2 = 1;
                                        break;
                                    }
                                }

                                // 

                                if (pc2.skill[target] === "") {

                                    if (bonda === 1) {
                                        this.text += `${pc1.playerName}0\n`;
                                        break;
                                    } else {
                                        target2 = use;
                                    }
                                }


                                // 
                                if (pc2.skill[target] === "") {
                                    let bestDiff2 = 999;
                                    let bestTarget2 = -1;
                                    let useLV2 = target + 1;

                                    for (let j2 = 0; j2 < pc1.getSkillsLength(); j2++) {
                                        if (pc1.type[j2] !== Player.NONE && pc1.scar[j2] !== 2) {
                                            let targetLV2 = j2 + 1;
                                            let diff2 = Math.abs(useLV2 - targetLV2);

                                            if (diff2 < bestDiff2) {
                                                bestDiff2 = diff2;
                                                bestTarget2 = j2;
                                            } else if (diff2 === bestDiff2) {
                                                if (targetLV2 > (bestTarget2 + 1)) {
                                                    bestTarget2 = j2;
                                                }
                                            }
                                        }
                                    }

                                    if (bestTarget2 !== -1) {
                                        target2 = bestTarget2;
                                    } else {
                                        suspend2 = 1;
                                    }
                                }


                                if (suspend2 === 1) {
                                    suspend2 = 0;
                                    break;
                                }


                                this.text +=
                                    `${pc1.playerName}${pc1.name[target2]}${target2 + 1}\n`;
                                pc1.scar[target2] = 2;
                            }

                        }

                        // 

                        if (pc2.skill[target] === "") {
                            pc1.roubai = 2;
                        }

                        if (pc2.skill[target] === "") {
                            pc1.stan = 2;
                        }

                        if (pc2.skill[target] === "") {
                            pc1.ekibyo = 2;
                        }

                        if (pc2.skill[target] === "") {
                            pc2.stan = 2;
                        }

                        if (pc2.skill[target] === "") {
                            pc1.suijaku = 2;
                        }

                        if (i_damage_loop < damage - 1) {
                            this.text += `${pc1.playerName}${pc1.name[use]}${use + 1}\n`;
                        }
                        suspend = 1;

                    } else {

                        this.text += `${pc2.playerName}${pc2.name[target]}${target + 1}\n`;

                    }
                }
            }

            if (suspend === 1) {
                suspend = 0;
                break;
            }

        }


        // 2
        for (let i = 0; i < pc1.getSkillsLength(); i++) {
            if (pc1.scar[i] === 2) pc1.scar[i] = 1;
        }

        for (let i = 0; i < pc2.getSkillsLength(); i++) {
            if (pc2.scar[i] === 2) pc2.scar[i] = 1;
        }


        // 

        if (pc1.skill[use] === "") {
            pc1.stan = 2;
        }

        if (use < pc1.getSkillsLength() - 1 && pc1.skill[use + 1] === "") {
            pc1.tate = 2;
            pc1.limited[use + 1] = 2;

        }

        if (counterOn === 0) { // 


            if (pc1.skill[use] === "") {

                pc2.suijaku = 2;

            }

        }

        // 


        if (pc1.skill[use] === "") {
            pc1.kakugo = 2;
        }

        if (pc1.skill[use] === "") {
            pc1.bouheki_ = 2;
        }

        if (pc1.skill[use] === "") {
            pc2.stan = 2;
            pc2.roubai = 2;
            pc2.suijaku = 2;
        }

        if (pc1.skill[use] === "") {
            for (let i = 0; i < pc2.getSkillsLength(); i++) {
                if (pc2.type[i] !== Player.NONE) {

                    for (let j = 0; j < pc2.getSkillsLength(); j++) {
                        if (pc2.skill[j] === pc2.skill[i]) pc2.kage[j] = 2;
                    }

                    break;
                }
            }
        }

        counterOn = 0;


    }

    effect(pc1: Player, pc2: Player): number {


        let flag = 0;

        if (pc1.kakugo === 2) {
            this.text += `${pc1.playerName}\n`;
            pc1.kakugo = 1;
            flag = 1;
        }

        if (pc1.bouheki_ === 2) {
            this.text += `${pc1.playerName}\n`;
            this.text += `${pc1.playerName}\n`;
            this.text += `${pc1.playerName}\n`;
            pc1.bouheki += 3;
            pc1.bouheki_ = 0;
            flag = 1;
        }

        if (pc1.tate === 2) {
            this.text += `${pc1.playerName}\n`;
            this.text += `${pc1.playerName}\n`;
            pc1.bouheki += 2;
            pc1.tate = 0;
            flag = 1;
        }

        if (pc1.stan === 2) {
            this.text += `${pc1.playerName}\n`;
            pc1.stan = 1;
            flag = 1;
        }

        if (pc1.roubai === 2) {
            this.text += `${pc1.playerName}\n`;
            pc1.roubai = 1;
            flag = 1;
        }

        if (pc1.suijaku === 2) {
            this.text += `${pc1.playerName}\n`;
            pc1.suijaku = 1;
            flag = 1;
        }

        for (let i = 0; i < pc2.getSkillsLength(); i++) {
            if (pc2.kage[i] === 2) {
                this.text += `${pc2.playerName}${pc2.name[i]}${i + 1}\n`;
                pc2.kage[i] = 1;
                flag = 1;
            }
        }

        if (pc1.ekibyo === 2) {
            for (let i = 0; i < pc1.getSkillsLength(); i++) {
                if (pc1.type[i] !== Player.NONE) {
                    if (pc1.limited[i] >= 1) break;
                    this.text +=
                        `${pc1.playerName}${pc1.name[i]}${i + 1}${i + 1}\n`;
                    pc1.skill[i] = "";
                    pc1.name[i] = "";
                    pc1.type[i] = Player.COUNTER;
                    pc1.speed[i] = i + 1;
                    pc1.damage[i] = 0;
                    pc1.limited[i] = 0;
                    break;
                }
            }
            pc1.ekibyo = 0;
            flag = 1;
        }


        return flag;
    }

    breakup(pc1: Player): number {

        let flag = 0;

        for (let i = 0; i < pc1.getSkillsLength(); i++) {

            if (pc1.scar[i] === 1) {

                if (pc1.type[i] !== Player.ENCHANT && i < pc1.getSkillsLength() - 1 && pc1.skill[i + 1] === "") {
                    this.text +=
                        `${pc1.playerName}${i + 2}${pc1.name[i]}${i + 1}\n`;
                    this.text += `${pc1.playerName}${i + 2}\n`;

                    pc1.skill[i + 1] = "";
                    pc1.name[i + 1] = "";
                    pc1.type[i + 1] = Player.NONE;
                    pc1.speed[i + 1] = 0;
                    pc1.damage[i + 1] = 0;
                    pc1.scar[i + 1] = 0;
                    pc1.limited[i + 1] = 0;
                    pc1.scar[i] = 0;


                } else {

                    this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;

                    if (pc1.skill[i] === "") {
                        this.text += `${pc1.playerName}${pc1.name[i]}${i + 1}\n`;
                        this.text += `${pc1.playerName}\n`;
                        pc1.gekirin++;
                    }

                    pc1.skill[i] = "";
                    pc1.name[i] = "";
                    pc1.type[i] = Player.NONE;
                    pc1.speed[i] = 0;
                    pc1.damage[i] = 0;
                    pc1.scar[i] = 0;
                    pc1.limited[i] = 0;
                    pc1.kage[i] = 0;
                }

                flag = 1;
            }

        }

        return flag;
    }

    judge(pc1: Player, pc2: Player, prior: number): number {

        // File operations are removed for web environment

        let flag = 0;
        let result = 0;
        for (let i = 0; i < pc2.getSkillsLength(); i++) {
            if (pc2.type[i] !== Player.NONE) flag = 1;
        }
        if (flag === 0) result += 1; // PC2PC1

        flag = 0;
        for (let i = 0; i < pc1.getSkillsLength(); i++) {
            if (pc1.type[i] !== Player.NONE) flag = 1;
        }
        if (flag === 0) result += 2; // PC1PC2

        if (result > 0) {

            this.text += "\n\n\n";


            if (Battle.SHORT === 0) {
                for (let i = 0; i < pc1.getSkillsLength(); i++) this.text += `${pc1.name[i]}`;
            } else {
                for (let i = 0; i < pc1.getSkillsLength(); i++) this.text += pc1.skill[i];
            }

            this.text += `${pc1.playerName}`;

            this.text += "\n";

            if (Battle.SHORT === 0) {
                for (let i = 0; i < pc2.getSkillsLength(); i++) this.text += `${pc2.name[i]}`;
            } else {
                for (let i = 0; i < pc2.getSkillsLength(); i++) this.text += pc2.skill[i];
            }

            this.text += `${pc2.playerName}`;

            this.text += "\n\n";

        }

        if (result === 3) {
            if (prior === 1) result = 2; // 1
            else if (prior === 2) result = 1; // 1
            else result = 3; // 
        }

        if (result === 1) {
            this.text += `${pc1.playerName}\n\n`;
            this.text += "=============================================\n";
            return 1;
        } else if (result === 2) {
            this.text += `${pc2.playerName}\n\n`;
            this.text += "=============================================\n";
            return 2;
        } else if (result === 3) {
            this.text += "\n\n";
            this.text += "=============================================\n";
            return 3;
        }

        return 0; // 
    }
}
