import React, { useState } from 'react';
import { ALL_SKILLS, STATUS_DATA } from './skillsData';

interface RuleProps {
  onClose: () => void;
}

export const Rule: React.FC<RuleProps> = ({ onClose }) => {
  const [activeTab, setActiveTab] = useState(0);

  const menuItems = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    ""
  ];

  const renderContent = () => {
    switch (activeTab) {
      case 0:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <div style={{ textAlign: 'center', marginBottom: '20px' }}>
                <div style={{ background: '#333', padding: '20px', borderRadius: '10px', border: '1px dashed #666', color: '#888' }}>
                    [  ]
                </div>
            </div>
            <p style={{ fontSize: '1.2rem', color: '#ffeb3b', fontWeight: 'bold' }}></p>
            <p></p>
            <p style={{ fontSize: '1.2rem', color: '#ffeb3b', fontWeight: 'bold' }}></p>
            <p></p>
            <p style={{ fontSize: '1.2rem', color: '#ffeb3b', fontWeight: 'bold' }}></p>
            <p></p>
          </div>
        );
      case 1:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <div style={{ height: '400px', overflowY: 'auto', paddingRight: '10px' }}>
              {ALL_SKILLS.map(skill => (
                <div key={skill.abbr} style={{ marginBottom: '20px', padding: '15px', background: 'rgba(255,255,255,0.05)', borderRadius: '8px', borderLeft: '4px solid #4fc3f7' }}>
                  <div style={{ display: 'flex', alignItems: 'center', gap: '15px', marginBottom: '10px' }}>
                    <img src={(process.env.PUBLIC_URL || '') + skill.icon} alt="" style={{ width: '40px', height: '40px' }} />
                    <div>
                      <div style={{ fontWeight: 'bold', fontSize: '1.1rem', color: '#4fc3f7' }}>{skill.name}</div>
                      <div style={{ fontSize: '0.8rem', color: '#aaa' }}>{skill.type} / : {skill.speed}</div>
                    </div>
                  </div>
                  <div style={{ fontSize: '0.9rem', lineHeight: '1.4' }}>{skill.description.replace('', '').replace('', '').replace('', '')}</div>
                </div>
              ))}
            </div>
          </div>
        );
      case 2:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <ol style={{ lineHeight: '2' }}>
              <li><strong style={{ color: '#4fc3f7' }}></strong> </li>
              <li><strong style={{ color: '#4fc3f7' }}></strong> </li>
              <li><strong style={{ color: '#4fc3f7' }}></strong> 
                <ul style={{ fontSize: '0.9rem', color: '#ccc' }}>
                  <li><span style={{ color: '#ffeb3b' }}></span></li>
                </ul>
              </li>
              <li><strong style={{ color: '#4fc3f7' }}></strong> </li>
            </ol>
          </div>
        );
      case 3:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <p></p>
            <ul style={{ lineHeight: '2' }}>
              <li><strong style={{ color: '#66bb6a' }}></strong> </li>
              <li><strong style={{ color: '#ef5350' }}></strong> </li>
              <li><strong style={{ color: '#aaa' }}></strong> 10</li>
            </ul>
          </div>
        );
      case 4:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <p><span style={{ color: '#ef5350', fontWeight: 'bold' }}></span></p>
            <p></p>
            <div style={{ marginTop: '20px', padding: '15px', background: 'rgba(255,82,82,0.1)', borderRadius: '8px', border: '1px solid #ff5252' }}>
                0
            </div>
          </div>
        );
      case 5:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <div style={{ display: 'grid', gap: '15px' }}>
                <div style={{ padding: '10px', background: 'rgba(255,255,255,0.05)', borderRadius: '5px' }}>
                    <strong style={{ color: '#ef5350' }}></strong> 
                </div>
                <div style={{ padding: '10px', background: 'rgba(255,255,255,0.05)', borderRadius: '5px' }}>
                    <strong style={{ color: '#66bb6a' }}></strong> 
                </div>
                <div style={{ padding: '10px', background: 'rgba(255,255,255,0.05)', borderRadius: '5px' }}>
                    <strong style={{ color: '#4fc3f7' }}></strong> 
                </div>
            </div>
          </div>
        );
      case 6:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <div style={{ height: '400px', overflowY: 'auto' }}>
              {STATUS_DATA.map(status => (
                <div key={status.name} style={{ marginBottom: '15px', padding: '10px', background: 'rgba(255,255,255,0.05)', borderRadius: '5px' }}>
                    <strong style={{ color: '#ffeb3b' }}>{status.name.replace('', '')}</strong>
                    <span style={{ fontSize: '0.9rem' }}>{status.description.replace('', '').replace('', '').replace('', '')}</span>
                </div>
              ))}
            </div>
          </div>
        );
      case 7:
        return (
          <div className="rule-content-section">
            <h2></h2>
            <div style={{ textAlign: 'center', marginTop: '50px' }}>
                <p> 2026 Shiden-Game Project</p>
                <p>All Rights Reserved.</p>
                <p style={{ fontSize: '0.8rem', color: '#888', marginTop: '30px' }}></p>
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div style={{
      position: 'fixed', top: 0, left: 0, width: '100%', height: '100%',
      backgroundColor: 'rgba(0,0,0,0.85)', zIndex: 20000,
      display: 'flex', alignItems: 'center', justifyContent: 'center',
      padding: '20px', boxSizing: 'border-box'
    }}>
      <div style={{
        width: '100%', maxWidth: '900px', height: '600px',
        backgroundColor: '#1a1a1a', border: '2px solid #4fc3f7',
        borderRadius: '15px', display: 'flex', overflow: 'hidden',
        position: 'relative', boxShadow: '0 0 30px rgba(79, 195, 247, 0.3)'
      }}>
        {/* Sidebar */}
        <div style={{
          width: '200px', backgroundColor: '#111', borderRight: '1px solid #333',
          display: 'flex', flexDirection: 'column', flexShrink: 0
        }}>
          <div style={{ padding: '20px', fontWeight: 'bold', color: '#4fc3f7', borderBottom: '1px solid #333', textAlign: 'center' }}>
            RULE BOOK
          </div>
          <div style={{ flex: 1, overflowY: 'auto' }}>
            {menuItems.map((item, index) => (
              <div
                key={index}
                onClick={() => setActiveTab(index)}
                style={{
                  padding: '15px 20px', cursor: 'pointer', fontSize: '0.9rem',
                  backgroundColor: activeTab === index ? 'rgba(79, 195, 247, 0.1)' : 'transparent',
                  color: activeTab === index ? '#4fc3f7' : '#ccc',
                  borderLeft: activeTab === index ? '4px solid #4fc3f7' : '4px solid transparent',
                  transition: 'all 0.2s'
                }}
              >
                {item}
              </div>
            ))}
          </div>
        </div>

        {/* Content Area */}
        <div style={{ flex: 1, padding: '30px', position: 'relative', overflowY: 'hidden', display: 'flex', flexDirection: 'column' }}>
          <button 
            onClick={onClose}
            style={{
              position: 'absolute', top: '15px', right: '15px',
              background: 'none', border: 'none', color: '#888',
              fontSize: '24px', cursor: 'pointer'
            }}
          >
            
          </button>
          <div style={{ flex: 1, overflowY: 'auto' }}>
            {renderContent()}
          </div>
        </div>
      </div>
    </div>
  );
};
